<template>
  <section class="filter-container">
    <v-card>
      <v-toolbar class="filter" color="#1d1a1a" dark flat prominent height="100">
        <v-text-field
          class="mx-4"
          flat
          hide-details
          label="Search"
          solo-inverted
          @input="doFilter()"
          v-model="filterby.name"
        ></v-text-field>

        <v-tabs slot="extension" color="white" background-color="transparent" centered>
          <v-tab @click="setAll()" v-model="filterby.isAll">All</v-tab>
          <v-tab @click="setStock()" v-model="filterby.inStock">InStock</v-tab>
          <v-overflow-btn @change="setType()" v-model="filterby.type" :items="types" label="Select type" hide-details class="pa-0"></v-overflow-btn>
        </v-tabs>
      </v-toolbar>
    </v-card>
  </section>
</template>



<script>
export default {
  data() {
    return {
      filterby: {
        isAll: true,
        name: "",
        inStock: false,
        type: ""
      },
      types:["Educational", "Adult", "Funny","General"]
    };
  },
  methods: {
    doFilter() {      
      this.$store.commit({type:'setFilter',filter:this.filterby})
    },
    setStock(){
      this.filterby.inStock=true;
      this.filterby.isAll=false;
      this.doFilter()
    },
    setAll(){
      this.filterby.inStock=false;
      this.filterby.isAll=true;
      this.doFilter()
    },
    setType(){
      this.filterby.isAll=false;
      this.doFilter();
    }

  }
};
</script>
