<template>
<section class="toy-preview-container">
  <v-card
    class="mx-auto toy-container"
    max-width="344"
    outlined
  >
    <v-list-item three-line>
      <v-list-item-content>
        <div class="overline mb-4"><span v-if="toy.inStock">In stock</span>
        <span v-else>Out of stock</span>
        </div>
        <v-list-item-title class="headline mb-1">{{toy.name}}</v-list-item-title>
        <v-list-item-subtitle>Greyhound divisely hello coldly fonwderfully</v-list-item-subtitle>
        <v-list-item-title>{{toy.type}}</v-list-item-title>

      </v-list-item-content>

      <v-list-item-avatar
        tile
        size="80"
      ><img :src="toy.imgUrl"></v-list-item-avatar>
    </v-list-item>

    <v-card-actions>
      <v-btn @click="remove(toy.id)" text>Remove</v-btn>
      <v-btn @click="edit(toy.id)" text>Edit</v-btn>
    </v-card-actions>
  </v-card>
  </section>
</template>

<script>
import '../scss/global.scss'

export default {
  props:['toy'],
  methods:{
    remove(id){
      this.$emit('remove',id)
    },
    edit(id){
      this.$emit('edit',id) 
    }
  }
}
</script>

