


<script>
import { Bar } from "vue-chartjs";
export default {
  extends: Bar,
  props: {
    height: "30",
    padding: "50"
  },
  methods: {
    getToys() {
      var toys = this.$store.getters.toys;
      var toysToShow = toys.reduce((acc,toy)=>{          
          acc.name.push(toy.name);
          acc.price.push(toy.price);
          return acc
      },{name:[],price:[]})
      return toysToShow
      
    },
  },
  mounted() {
    var toys = this.getToys();
    
    // Overwriting base render method with actual data.
    this.renderChart({
       labels: toys.name,

      datasets: [
        {
          label: "Our toys prices",
          backgroundColor: "#f87979",
          data: toys.price
        }
      ]
    });
  }
};
</script>

 

