<template>
  <v-content v-if="toyToEdit">
    <form class="edit-container">
      <v-text-field v-model="toyToEdit.name" label="Toy name"></v-text-field>

      <v-text-field v-model="toyToEdit.price" v-mask="mask" label="Price"></v-text-field>

      <!-- <v-text-field v-model="toyToEdit.price" label="Toy price"></v-text-field> -->
      <v-text-field v-model="toyToEdit.imgUrl" label="Toy Img"></v-text-field>
      <v-select v-model="toyToEdit.type" :items="items" label="Type"></v-select>
      <v-checkbox v-model="toyToEdit.inStock" label="In stock?"></v-checkbox>

      <v-btn class="mr-4" @click="submit">submit</v-btn>
    </form>
  </v-content>
</template>


<script>

import { mask } from 'vue-the-mask'

export default {
  directives: {
      mask,
    },
    
  data() {
    return {
      toyToEdit: null,
      items: ["Educational", "Adult", "Funny"],
      checkbox: false,
      mask:'############'
    };
  },
  methods: {
    submit() {
      if (this.$route.params.id) {
        this.$store
          .dispatch({ type: "editToy", toy: this.toyToEdit })
          .then(this.$router.push("/"));
      } else{
        if(this.toyToEdit.price) this.toyToEdit.price = +this.toyToEdit.price
        this.$store.dispatch({ type: "addToy", toy: this.toyToEdit })
          .then(this.$router.push("/"));
        
      }
    }
  },

  created() {
    if (this.$route.params.id)
      this.toyToEdit = JSON.parse(JSON.stringify(this.$store.getters.currToy));
    else this.toyToEdit = {};
  }
};
</script>


<style scopped>
  .edit-container{
    margin: 50px;
    text-align: center;
  }
</style>