'use strict'

import {httpService} from './http.service.js';

export default {
    query,
    getById,
    remove,
    edit,
    add
}


const BASE_URL = (process.env.NODE_ENV !== 'development')
 ? '/api/toy'
 : '//localhost:3000/api/toy';

function query(){
    return httpService.get(`${BASE_URL}`)
        .then(res => res.data)
}

function getById(id){
    return httpService.get(`${BASE_URL}/${id}`)
        .then(res => res.data)
}


function remove(id){
    return httpService.delete(`${BASE_URL}/${id}`)
}

function edit(toy){
    return httpService.put(`${BASE_URL}/${toy.id}`,toy)
        .then(res => res.data)
}


function add(toy){
    return httpService.post(`${BASE_URL}`,toy)
        .then(res=> res.data)
}
